.page {
  background-color: #fff;
  min-height: 100vh;
  background-size: cover;
  color: #252B42;
  box-sizing: border-box;
  font-family: 'Inter', Arial, sans-serif;
}
@media screen and (max-width: 767px) {
    .page {
      max-width: 100%;
      height: 100%;  
    }
}
.header {
  display: flex;
  align-items: center;
  width: 100%;
  height: 7.741vh;
  border-bottom: 1px solid black;
  justify-content: space-between;
  padding: 0 4.861vw;
}
.header-btns {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: flex-end;
}
.header-contacts-btn {
    background-color: white;
    width: 186px;
    height: 40px;
    border: none;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 15px;
    text-align: center;
    letter-spacing: 0.2px;
    color: #737373;
    opacity: 1;
    transition: .3s opacity;
}

.header-contacts-btn:hover {
    opacity: 0.8;
}

@media screen and (max-width: 200px) {
    .header-contacts-btn {
        font-size: 14px;
        line-height: 17px;
    }
}
.header-login-btn {
    width: 186px;
    height: 40px;
    background: #FFFFFF;
    border: 2px solid #FF685B;
    border-radius: 7px;
    cursor: pointer;
    color: #FF685B;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    align-items: center;
    text-align: center;
    opacity: 1;
    transition: .3s opacity;
    border-radius: 7px;
}
.header-login-btn:hover {
    opacity: 0.8;
}

@media screen and (max-width: 200px) {
    .header-login-btn {
        font-size: 14px;
        line-height: 17px;
    }
}
.header__logo {
    width: 53.27px;
    height: 22.66px;
    margin: 3.027vh 0 2.441vh 4.931vw
}
@media screen and (max-width: 375px) {
    .header__logo {
      width: 53.27px;
      height: 22.66px;
    }
}
.content {
    max-width: 80%;
    margin: 0 auto;
    padding-top: 25px;
}
.main {
    display: flex;
    flex-flow: column nowrap;
}
.main-btns {
    display: grid; 
    grid-template-columns: 138px auto;
    grid-gap: 10px ;
    gap: 10px ; 
}
.main-title {
    min-height: 89px;
    min-width: 355px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 58px;
    line-height: 80px;
    letter-spacing: 0.2px;
    color: #252B42;
    max-width: 727px;
    max-height: 160px;
}
@media screen and (max-width: 760px) {
    .main-title {
        font-size: 48px;
        line-height: 50px;
    }
}
@media screen and (max-width: 655px) {
    .main-title {
        font-size: 38px;
        line-height: 45px;
    }
}
@media screen and (max-width: 519px) {
    .main-title {
        font-size: 28px;
        line-height: 39px;
    }
}
.main-login-btn {
    background-color: #FF685B;
    width: 138px;
    height: 52px;
    align-items: center;
    color: #FFFFFF;
    font-style: 'Montserrat';
    font-weight: normal;
    font-size: 14px;
    line-height: 22px;
    letter-spacing: 0.2px;
    transition: .3s opacity;
    border-radius: 5px;
    border: 2px;
}
.main-login-btn:hover {
    opacity: 0.8;
}

@media screen and (max-width: 200px) {
    .lmain-login-btn {
        font-size: 14px;
        line-height: 17px;
    }
}
.main-contacts-btn {
    background-color: white;
    width: 155px;
    height: 52px;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    color: #FF685B;
    font-style: 'Montserrat';
    font-weight: normal;
    font-size: 14px;
    line-height: 22px;
    letter-spacing: 0.2px;
    opacity: 1;
    transition: .3s opacity;
    border-radius: 5px;
    border: 2px solid #FF685B;
}
.main-contacts-btn:hover {
    opacity: 0.8;
}

@media screen and (max-width: 200px) {
    .main-contacts-btn {
        font-size: 14px;
        line-height: 17px;
    }
}
.contacts {
    width: 727px;
    height: 71px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 58px;
    line-height: 80px;
    letter-spacing: 0.2px;
    color: #252B42;
}
.elements {
    max-width: 880px;
    margin: 0;
    display: grid;
    grid-template-columns: repeat(3, 328px);
    padding-top: 35px;
    grid-gap: 30px;
    gap: 30px;
}
@media screen and (max-width: 902px) {
    .elements {
        max-width: 600px;
        grid-template-columns: repeat(2, 328px);
    }
}
@media screen and (max-width: 734px) {
    .elements {
      max-width: 320px;
      grid-template-columns: 328px;
    }
}
@media screen and (max-width: 375px) {
    .elements {
      max-width: 328px;
    }
}
.element {
    border-radius: 10px;
    background: #FFFFFF;
    position: relative;
    box-shadow: 0px 13px 19px rgba(0, 0, 0, 0.07);
    background: #FFFFFF;
}
.element__info {
    display: flex;
    flex-flow: column nowrap;
    margin: 0;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: white;
    color: #E64040;
    justify-content: space-between;
    align-items: flex-start;
    padding: 20px 20px 26px 40px;
    z-index: 1;
}
.element__image {
    width: 72px;
    height: 72px;
    display: block;
    background: #E74040;
    border-radius: 200px;
    margin-left: 40px;
    margin-right: auto;
    margin-top: 30px;

    
}
.element__title {
    margin: 0;
    width: 132px;
    height: 44px;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    color: #252B42;
    text-overflow: ellipsis;
    white-space: nowrap;
    border-bottom: 2px solid #E74040;
}

.element__text {
    width: 81px;
    height: 20px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.2px;
    color: #737373;
    padding-top: 20px;
}
.element__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}
.popup {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    display: flex;
    background-color: rgba(0, 0, 0, 0.5);
    justify-content: center;
    align-items: center;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.2s, opacity 0.2s linear;
}
.popup_opened {
    visibility: visible;
    opacity: 1;
}
.popup__container {
    box-sizing: border-box;
    width: 35%;
    height: 50%;
    max-height: 400px;
    min-width: 280px;
    max-width: 500px;
    background-color: white;
    border-radius: 10px;
    position: relative;
    padding: 34px 36px 37px;
}

@media screen and (max-width: 80%) {
    .popup__container {
      margin: 0 19px;
      padding: 25px 22px;
    }
}
.popup__title {
    margin-top: 0;
    margin-bottom: 54px;
    font-weight: 900;
    font-size: 24px;
    color: #000000;
}
@media screen and (max-width: 620px) {
    .popup__title {
        font-size: 18px;
        line-height: 22px;
    }
}
.popup__input {
    border: 0;
    width: 100%;
    height: 24px;
    box-sizing: border-box;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    color: #000000;
}
.popup__input:last-of-type {
    margin-top: 30px;
}

.popup__save-btn {
    background-color: #FF685B;
    width: 100%;
    height: 50px;
    border: 0;
    margin: 48.34px 0 0 0;
    padding-bottom: 5px;
    cursor: pointer;
    color: white;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    opacity: 1;
    transition: .3s opacity;
    border-radius: 2px;
}
.popup__save-btn:hover {
    opacity: 0.8;
}

@media screen and (max-width: 610px) {
    .popup__save-btn {
        font-size: 14px;
        line-height: 17px;
    }
}
.profile {
    display: flex;
    flex-flow: column wrap;
    align-items: flex-start;
    max-width: 1046px;
    margin: 7.815vh 13.68vw auto;
}
.profile-btns {
    display: grid;
    grid-template-columns: 228px auto;
    grid-gap: 10px;
    gap: 10px;
}
@media screen and (max-width: 902px) {
    .profile {
        max-width: 600px;
        flex-direction: column;
    }
    .profile-btns {
        grid-template-columns: 228px;
    }
}

.profile__exit-btn {
    background-color: #FF685B;
    width: 228px;
    height: 52px;
    align-items: center;
    color: #FFFFFF;
    font-style: 'Montserrat';
    font-weight: normal;
    font-size: 14px;
    line-height: 22px;
    letter-spacing: 0.2px;
    transition: .3s opacity;
    border-radius: 5px;
    border: 2px;
}

.profile__exit-btn:hover {
    opacity: 0.8;
}
@media screen and (max-width: 885px) {
    .profile__exit-btn {
        width: 228px;
    }
}
.profile__to-contacts-btn {
    background-color: white;
    width: 228px;
    height: 52px;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    color: #FF685B;
    font-style: 'Montserrat';
    font-weight: normal;
    font-size: 14px;
    line-height: 22px;
    letter-spacing: 0.2px;
    opacity: 1;
    transition: .3s opacity;
    border-radius: 5px;
    border: 2px solid #FF685B;
}

.profile__to-contacts-btn:hover {
    opacity: 0.8;
}
@media screen and (max-width: 885px) {
    .profile__to-contacts-btn {
        width: 228px;
    }
}
.profile__title {
    width: 727px;
    height: 71px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 58px;
    line-height: 80px;
    letter-spacing: 0.2px;
    color: #252B42;
}

@media screen and (max-width: 440px) {
    .profile__title {
        font-size: 37px;
        line-height: 33px;
        text-align: center;
        max-width: 217px;
    }
}



