.main-title {
    min-height: 89px;
    min-width: 355px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 58px;
    line-height: 80px;
    letter-spacing: 0.2px;
    color: #252B42;
    max-width: 727px;
    max-height: 160px;
}
@media screen and (max-width: 760px) {
    .main-title {
        font-size: 48px;
        line-height: 50px;
    }
}
@media screen and (max-width: 655px) {
    .main-title {
        font-size: 38px;
        line-height: 45px;
    }
}
@media screen and (max-width: 519px) {
    .main-title {
        font-size: 28px;
        line-height: 39px;
    }
}